* {
    margin: 0;
}
.wrapper {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center; 
    background: url("../src/background-image.jpg") center center no-repeat;
    background-size: cover;
 }
 .container {
    width: 50vw;
    text-align: center;
    padding: 4% 2%;
    backdrop-filter: blur(20px);
    border-radius: 8%;
 }

 h1 {
    font-weight: 400;
    margin: 0px 0 50px;
 }

 input[type="text"] {
    background-color: transparent;
    border: 0;
    border-bottom: solid 1px #f15186;
    width: 40%;
    padding-bottom: 4px;
    color: #fff;
    font-weight: lighter;
    letter-spacing: 2px;
    margin-bottom: 3%;
    margin-right: 2%;
    font-size: 20px;
 }

    button {
        width: 40%;
        border: 0;
        padding: 8px 20px;
        margin: 0 2px;
        border-radius: 2px;
        letter-spacing: 1px;
        font-size: 1.5rem;
        cursor: pointer;
        background-color: #f15186;
        color: #fff;
    }
    button:hover {
        opacity: 0.9
    }
    *:focus {
        outline: none;
    }
    .results-city {
        font-size: 4rem;
    }
    .results-country {
        font-size: 2rem;
    }
    .results-temp {
        font-size: 6rem;
        margin: 10px 0;
        color: #f15186;
    }
    .results-temp > span {
        font-size: 3rem;
        color: #333;
    }
    .results-condition {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 2.5rem;
    }
    
    .loading {
        border: 4px solid #f15186;
        border-top: 4px solid #ffffff;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        margin: auto;
        animation: spin 2s linear infinite;
    }
    @keyframes spin {
        0% { transform: rotate(0deg); }
        100% { transform: rotate(360deg); }
    }    